// src/features/req-tree/reqTreeSlice.ts

import { createSlice } from "@reduxjs/toolkit";
import { Requirement } from "../../app/types";

// 
interface ReqTreeState {
  requirements: Requirement[];
  selectedReqId: string | null;
}

// 
const initialState: ReqTreeState = {
  requirements: [],
  selectedReqId: null,
};

export const reqTreeSlice = createSlice({
  name: "reqTree",
  initialState,
  reducers: {
    setRequirements: (state, action) => {
      return {
        ...state,
        requirements: action.payload,
      };
    },
    setReqId: (state, action) => {
      return {
        ...state,
        selectedReqId: action.payload,
      };
    },
  },
});

// Action creators are generated for each case reducer function
export const { setRequirements, setReqId } = reqTreeSlice.actions;

export default reqTreeSlice.reducer;
