// src/features/glossary-list/GlossaryList.tsx

import { RootState } from "../../app/store";
import { useSelector } from "react-redux";
import { Box } from "@mui/system";
import TableBody from "@mui/material/TableBody";
import TableRow from "@mui/material/TableRow";
import Typography from "@mui/material/Typography";
import {
  StyledTableContainer,
  StyledTable,
  StyledTableCellKey,
  StyledTableCellValue,
} from "../../components/ReqTable";
import env from "../../env";

const GlossaryList = (): JSX.Element => {
  const glossaryCategory = useSelector(
    (state: RootState) => state.glossaryList.glossaryCategory
  );

  const uri2url = (uri: string): string => {
    return env.imageDirUrl + uri;
  };

  const renderImages = (imageUris: string[]): JSX.Element[] => {
    return imageUris.map((uri, index) => (
      <div>
        <img src={uri2url(uri)} alt="glossaryImages" width="400" />
        <br />
      </div>
    ));
  };

  const renderTermRows = (terms: any[]): JSX.Element[] => {
    return terms.map((term, index) => (
      <TableRow key={index}>
        <StyledTableCellKey>{term.term}</StyledTableCellKey>
        <StyledTableCellValue>{term.description}</StyledTableCellValue>
      </TableRow>
    ));
  };

  return (
    <div>
      {glossaryCategory && (
        <div>
          <Box my={2.4}>
            <Box mb={-0.6}>
              <Typography variant="subtitle2" color="textSecondary">
                
              </Typography>
            </Box>
            <Typography variant="h6">{glossaryCategory.category}</Typography>
          </Box>
          <Box my={2.4}>
            {glossaryCategory.images && renderImages(glossaryCategory.images)}
          </Box>
          <Box my={2.4}>
            <StyledTableContainer>
              <StyledTable>
                <TableBody>{renderTermRows(glossaryCategory.terms)}</TableBody>
              </StyledTable>
            </StyledTableContainer>
          </Box>
        </div>
      )}
    </div>
  );
};

export default GlossaryList;
