import { AppBar, Box, Toolbar, Typography, Link } from "@mui/material";
import { Route, BrowserRouter as Router, Routes } from "react-router-dom";
import { RootState } from "./app/store";
import { useDispatch } from "./app/hooks";
import { useSelector } from "react-redux";
import ReqList from "./features/req-list/ReqList";
import ReqTree from "./features/req-tree/ReqTree";
import GlossaryList from "./features/glossary-list/GlossaryList";
import { loadData } from "./features/digest/digestSlice";
import GlossaryTree from "./features/glossary-tree/GlossaryTree";
import { setRequirements } from "./features/req-tree/reqTreeSlice";
import { setGlossary } from "./features/glossary-tree/glossaryTreeSlice";

function App() {
  const loaded = useSelector((state: RootState) => state.digest.loaded);
  const requirements = useSelector(
    (state: RootState) => state.digest.requirementRoot?.requirements
  );
  const glossary = useSelector((state: RootState) => state.digest.glossary);
  const dispatch = useDispatch();

  // 
  if (!loaded) {
    dispatch(loadData());
  }
  dispatch(setRequirements(requirements));
  dispatch(setGlossary(glossary));

  return (
    <Router>
      <Box
        sx={{ display: "flex", flexDirection: "column", minHeight: "100vh" }}
      >
        <AppBar
          position="fixed"
          sx={{ width: "100%", zIndex: (theme) => theme.zIndex.drawer + 1 }}
        >
          <Toolbar>
            <AppTitle />
          </Toolbar>
        </AppBar>
        <Box sx={{ display: "flex", flexGrow: 1, pt: 8 }}>
          <SideMenu />
          <Box component="main" sx={{ flexGrow: 1, p: 3 }}>
            <Routes>
              <Route path="/" element={<ReqList />} />
              <Route path="/reqList/*" element={<ReqList />} />
              <Route path="/glossary" element={<GlossaryList />} />
            </Routes>
          </Box>
        </Box>
        <Box
          component="footer"
          sx={{
            p: 2,
            mt: "auto",
            backgroundColor: "primary.main",
            color: "white",
            textAlign: "center",
            width: "100%",
          }}
        >
          <Footer />
        </Box>
      </Box>
    </Router>
  );
}

// 
const AppTitle = (): JSX.Element => {
  const project: any = useSelector((state: RootState) => state.digest.project);

  return (
    <Box>
      <Typography variant="h6" noWrap sx={{ mb: -0.8 }}>
        {project && project.pageTitle}
      </Typography>
      <Typography variant="caption" noWrap sx={{ mt: -0.8 }}>
        {project && (
          <Link
            href={project.url}
            target="_blank"
            rel="noopener noreferrer"
            sx={{ color: "lightgray" }}
          >
            {project.url}
          </Link>
        )}
      </Typography>
    </Box>
  );
};

// 
const SideMenu = (): JSX.Element => {
  return (
    <Box
      component="nav"
      sx={{
        width: 240,
        flexShrink: 0,
        backgroundColor: "#f0f0f0",
        height: "calc(100vh - 64px)",
        padding: 2,
      }}
    >
      <Box sx={{ mt: 4 }}>
        <Typography variant="subtitle1" sx={{ my: 2 }}>
          
        </Typography>
        <ReqTree />
      </Box>
      <Box sx={{ my: 4 }}>
        <Typography variant="subtitle1" sx={{ my: 2 }}>
          
        </Typography>
        <GlossaryTree />
      </Box>
    </Box>
  );
};

// 
const Footer = (): JSX.Element => {
  return (
    <Typography variant="body2">
      Generated by <code>campsite</code> developed by{" "}
      <Link
        href="https://alpaca.engineer/"
        target="_blank"
        rel="noopener noreferrer"
        sx={{ color: "lightgray" }}
      >
        ALPACA
      </Link>
    </Typography>
  );
};

export default App;
