// src/features/reg-tree/ReqTree.tsx

import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "../../app/hooks";
import { RootState } from "../../app/store";
import { SimpleTreeView } from "@mui/x-tree-view/SimpleTreeView";
import { TreeItem } from "@mui/x-tree-view/TreeItem";
import { Box } from "@mui/system";
import Typography from "@mui/material/Typography";
import Folder from "@mui/icons-material/Folder";
import FolderOpen from "@mui/icons-material/FolderOpen";
import SourceIcon from "@mui/icons-material/Source";
import { Requirement } from "../../app/types";
import { setReqId } from "./reqTreeSlice";
import { setRequirement } from "../req-list/reqListSlice";

const ReqTree = (): JSX.Element => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const requirements = useSelector(
    (state: RootState) => state.reqTree.requirements
  );
  const reqId = useSelector((state: RootState) => state.reqTree.selectedReqId);

  // RequirementsidRequirement
  const findRequirement = (
    requirements: Requirement[] | null | undefined,
    reqId: string
  ): Requirement | null => {
    if (requirements == null) {
      return null;
    }
    for (let req of requirements) {
      if (req.id === reqId) return req;
      if ("requirements" in req) {
        return findRequirement(req.requirements, reqId);
      }
    }
    return null;
  };

  // reqIdReqListState
  // ReqListreqId
  if (reqId) {
    const requirement = findRequirement(requirements, reqId);
    dispatch(setRequirement(requirement));
  }

  const handleClick = (reqId: string) => {
    dispatch(setReqId(reqId));
    navigate("/reqList");
  };

  const renderTreeItems = (
    items: Requirement[],
    handleClick: (id: string) => void
  ): JSX.Element[] => {
    return items.map((item) => (
      <TreeItem
        key={item.id}
        itemId={item.id}
        label={
          <div>
            <Box mb={-0.6}>
              <Typography variant="caption" color="textSecondary">
                {item.id}
              </Typography>
            </Box>
            <Typography variant="body2">{item.title}</Typography>
          </div>
        }
        onClick={() => handleClick(item.id)}
      >
        {item.requirements && renderTreeItems(item.requirements, handleClick)}
      </TreeItem>
    ));
  };

  return (
    <SimpleTreeView
      slots={{
        expandIcon: Folder,
        collapseIcon: FolderOpen,
        endIcon: SourceIcon,
      }}
    >
      {requirements && renderTreeItems(requirements, handleClick)}
    </SimpleTreeView>
  );
};

export default ReqTree;
