// src/features/glossary-list/glossaryListSlice.ts

import { createSlice } from "@reduxjs/toolkit";
import { GlossaryCategory } from "../../app/types";

// 
interface GlossaryListState {
  glossaryCategory: GlossaryCategory | null;
}

// 
const initialState: GlossaryListState = {
  glossaryCategory: null,
};

export const glossaryListSlice = createSlice({
  name: "glossaryList",
  initialState,
  reducers: {
    setGlossaryCategory: (state, action) => {
      return {
        ...state,
        glossaryCategory: action.payload,
      };
    },
  },
});

// Action creators are generated for each case reducer function
export const { setGlossaryCategory } = glossaryListSlice.actions;

export default glossaryListSlice.reducer;
