// src/features/req-list/ReqList.tsx

import { useSelector } from "react-redux";
import { RootState } from "../../app/store";
import { useNavigate, useLocation } from "react-router-dom";
import { useDispatch } from "../../app/hooks";
import Typography from "@mui/material/Typography";
import { Box } from "@mui/system";
import TableBody from "@mui/material/TableBody";
import TableRow from "@mui/material/TableRow";
import { Link } from "@mui/material";
import { Requirement, Specification } from "../../app/types";
import {
  StyledTableContainer,
  StyledTable,
  StyledTableCellKey,
  StyledTableCellValue,
  PreLineText,
} from "../../components/ReqTable";
import { setReqId } from "../req-tree/reqTreeSlice";
import env from "../../env";

const ReqList = (): JSX.Element => {
  // URLID
  const location = useLocation();
  const path = location.pathname.replace('/reqList/', '');
  const segments = path.split('/');
  console.log(segments);
  
  // segments "-" 
  const reqId = segments.join("-");

  const requirement = useSelector(
    (state: RootState) => state.reqList.requirement
  );

  const navigate = useNavigate();
  const dispatch = useDispatch();

  const handleReqIdClicked = (reqId: string | null) => {
    dispatch(setReqId(reqId));
    navigate("/reqList");
  };

  const uri2url = (uri: string): string => {
    return env.imageDirUrl + uri;
  };

  const renderImages = (imageUris: string[]): JSX.Element[] => {
    return imageUris.map((uri, index) => (
      <div>
        <img src={uri2url(uri)} alt="reqImages" width="400" />
        <br />
      </div>
    ));
  };

  const renderReqRows = (requirements: Requirement[]): JSX.Element[] => {
    return requirements.map((req, index) => (
      <TableRow key={req.id}>
        <StyledTableCellKey>
          <Link
            component="button"
            variant="body2"
            onClick={(event) => {
              event.preventDefault();
              handleReqIdClicked(req.id);
            }}
          >
            {req.id}
          </Link>
        </StyledTableCellKey>
        <StyledTableCellValue>{req.title}</StyledTableCellValue>
      </TableRow>
    ));
  };

  const renderSpecRows = (specifications: Specification[]): JSX.Element[] => {
    return specifications.map((spec, index) => (
      <TableRow key={spec.id}>
        <StyledTableCellKey>{spec.id}</StyledTableCellKey>
        <StyledTableCellValue>{spec.description}</StyledTableCellValue>
      </TableRow>
    ));
  };

  return (
    <div>
      {requirement && (
        <div>
          <Box my={2.4}>
            <Box mb={-0.6}>
              <Typography variant="subtitle2" color="textSecondary">
                {requirement.id}
              </Typography>
            </Box>
            <Typography variant="h6">{requirement.title}</Typography>
          </Box>

          <Box my={2.4}>
            <Typography variant="subtitle1"></Typography>
            <StyledTableContainer>
              <StyledTable>
                <TableBody>
                  <TableRow key="description">
                    <StyledTableCellKey></StyledTableCellKey>
                    <StyledTableCellValue>
                      <PreLineText variant="body2">
                        {requirement.description}
                      </PreLineText>
                      {requirement.images && renderImages(requirement.images)}
                    </StyledTableCellValue>
                  </TableRow>
                  <TableRow key="reason">
                    <StyledTableCellKey></StyledTableCellKey>
                    <StyledTableCellValue>
                      {requirement.reason}
                    </StyledTableCellValue>
                  </TableRow>
                </TableBody>
              </StyledTable>
            </StyledTableContainer>
          </Box>
        </div>
      )}
      {requirement && requirement.requirements && (
        <div>
          <Box my={2.4}>
            <Typography variant="subtitle1"></Typography>
            <StyledTableContainer>
              <StyledTable>
                <TableBody>{renderReqRows(requirement.requirements)}</TableBody>
              </StyledTable>
            </StyledTableContainer>
          </Box>
        </div>
      )}
      {requirement && requirement.specifications && (
        <div>
          <Box my={2.4}>
            <Typography variant="subtitle1"></Typography>
            <StyledTableContainer>
              <StyledTable>
                <TableBody>
                  {renderSpecRows(requirement.specifications)}
                </TableBody>
              </StyledTable>
            </StyledTableContainer>
          </Box>
        </div>
      )}
    </div>
  );
};

export default ReqList;
