import { configureStore } from "@reduxjs/toolkit";
import digestReducer from "../features/digest/digestSlice";
import reqTreeReducer from "../features/req-tree/reqTreeSlice";
import reqListReducer from "../features/req-list/reqListSlice";
import glossaryTreeReducer from "../features/glossary-tree/glossaryTreeSlice";
import glossaryListReducer from "../features/glossary-list/glossaryListSlice";

export const store = configureStore({
  reducer: {
    digest: digestReducer,
    reqTree: reqTreeReducer,
    reqList: reqListReducer,
    glossaryTree: glossaryTreeReducer,
    glossaryList: glossaryListReducer,
  },
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
