// src/features/glossary-tree/glossaryTreeSlice.ts

import { createSlice } from "@reduxjs/toolkit";
import { Glossary } from "../../app/types";

// 
interface GlossaryTreeState {
  glossary: Glossary | null;
  selectedCategoryIndex: number;
}

// 
const initialState: GlossaryTreeState = {
  glossary: null,
  selectedCategoryIndex: 0,
};

export const glossaryTreeSlice = createSlice({
  name: "glossaryTree",
  initialState,
  reducers: {
    setGlossary: (state, action) => {
      return {
        ...state,
        glossary: action.payload,
      };
    },
    setCategoryIndex: (state, action) => {
      return {
        ...state,
        selectedCategoryIndex: action.payload,
      };
    },
  },
});

// Action creators are generated for each case reducer function
export const { setGlossary, setCategoryIndex } = glossaryTreeSlice.actions;

export default glossaryTreeSlice.reducer;
