// src/features/digest/digestSlice.ts

import YAML from "yaml";
import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { Glossary, RequirementRoot } from "../../app/types";
import env from "../../env";

// 
interface DigestState {
  loaded: boolean;
  project: any | null;
  compilelog: any | null;
  requirementRoot: RequirementRoot | null;
  glossary: Glossary | null;
}

// 
const initialState: DigestState = {
  loaded: false,
  project: null,
  compilelog: null,
  requirementRoot: null,
  glossary: null,
};

export const digestSlice = createSlice({
  name: "digest",
  initialState,
  reducers: {},
  extraReducers: (builder) => {
    builder.addCase(loadData.fulfilled, (state, action) => {
      console.log(action.payload);
      state.loaded = true;
      state.project = action.payload.project;
      state.compilelog = action.payload.compilelog;
      state.requirementRoot = action.payload.requirementRoot;
      state.glossary = action.payload.glossary;
    });
  },
});

// digestURL
// Thunk
export const loadData = createAsyncThunk("digest/loadData", async () => {
  const response = await fetch(env.digestUrl);
  const yamlText = await response.text();
  return YAML.parse(yamlText);
});

// Action creators are generated for each case reducer function
// export const {} = digestSlice.actions;

export default digestSlice.reducer;
