// src/features/glossary-tree/GlossaryTree.tsx

import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "../../app/hooks";
import { RootState } from "../../app/store";
import Typography from "@mui/material/Typography";
import MenuBookIcon from "@mui/icons-material/MenuBook";
import { SimpleTreeView } from "@mui/x-tree-view/SimpleTreeView";
import { TreeItem } from "@mui/x-tree-view/TreeItem";
import { setCategoryIndex } from "./glossaryTreeSlice";
import { setGlossaryCategory } from "../glossary-list/glossaryListSlice";
import { Glossary, GlossaryCategory } from "../../app/types";

// Tree
// ReqTreeTreeItem
const GlossaryTree = (): JSX.Element => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const glossary = useSelector(
    (state: RootState) => state.glossaryTree.glossary
  );
  const categoryIndex = useSelector(
    (state: RootState) => state.glossaryTree.selectedCategoryIndex
  );

  // RequirementsidRequirement
  const findGlossaryCategory = (
    glossary: Glossary | null | undefined,
    index: number
  ): GlossaryCategory | null => {
    if (glossary == null) {
      return null;
    }
    return glossary.categories[index];
  };

  // categoryIndexGlossaryListState
  // ReqListcategoryIndex
  if (categoryIndex !== null) {
    const category = findGlossaryCategory(glossary, categoryIndex);
    dispatch(setGlossaryCategory(category));
  }

  const handleClick = (index: number) => {
    dispatch(setCategoryIndex(index));
    navigate("/glossary");
  };

  const renderGlossaryGategories = (categories: any[]): JSX.Element[] => {
    return categories.map((category, index) => (
      <TreeItem
        key={category + index}
        itemId={String(category + index)}
        label={
          <div>
            <Typography variant="body2">{category.category}</Typography>
          </div>
        }
        onClick={() => handleClick(index)}
      />
    ));
  };

  return (
    <div>
      <SimpleTreeView
        slots={{
          expandIcon: MenuBookIcon,
          collapseIcon: MenuBookIcon,
          endIcon: MenuBookIcon,
        }}
      >
        {glossary && renderGlossaryGategories(glossary.categories)}
      </SimpleTreeView>
    </div>
  );
};

export default GlossaryTree;
