// src/features/req-list/reqListSlice.ts

import { createSlice } from "@reduxjs/toolkit";
import { Requirement } from "../../app/types";

// 
interface ReqListState {
  requirement: Requirement | null;
}

// 
const initialState: ReqListState = {
  requirement: null,
};

export const reqListSlice = createSlice({
  name: "reqList",
  initialState,
  reducers: {
    setRequirement: (state, action) => {
      return {
        ...state,
        requirement: action.payload,
      };
    },
  },
});

// Action creators are generated for each case reducer function
export const { setRequirement } = reqListSlice.actions;

export default reqListSlice.reducer;
